}
:root {
  --ease-superpower: cubic-bezier(0.5, 0, 0.35, 1);
  --ease-superpower-bounce: cubic-bezier(0.34, 1.56, 0.64, 1);
}
@keyframes cta-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 119, 89, 0.4);
  }
  70% {
    box-shadow: 0 0 0 12px rgba(255, 119, 89, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 119, 89, 0);
  }
}
.animate-cta-pulse {
  animation: cta-pulse 2s ease-in-out infinite;
}
.card-group {
