@media (max-width: 768px) {
  :root {
    --section-padding-y: 64px;
  }
  .container {
    padding-left: var(--space-4);
    padding-right: var(--space-4);
  }
}
@media (max-width: 480px) {
  :root {
    --section-padding-y: 48px;
  }
}
